export default {
	data(){
		return {
			dataList:[]
		}
	},
	onShow(){
		this.dataList = [];
		this.$request.GET({
			url:this.$api.apiUrl.GET_WAP_MY_KECHECK
		}).then(res=>{
			if(res.code === 200){
				this.dataList = res.data;
			}else{
				this.$api.msg(res.msg)
			}
		})
	}
}
